import uniCollapse from "@/components/uni-collapse/uni-collapse.vue"
import uniCollapseItem from "@/components/uni-collapse-item/uni-collapse-item.vue"
import {
	mapState,
	mapMutations
} from 'vuex'

export default {
	components: {
		uniCollapse,
		uniCollapseItem
	},
	data() {
		return {
			user: ''
		}
	},
	computed: {
		...mapState(['hasLogin', 'forcedLogin', 'userName'])
	},
	onLoad() {
		this.user = this.userName;
	},
	methods: {
		...mapMutations(['logout']),
		bindLogin() {
			uni.navigateTo({
				url: '../login/login',
			});
		},
		bindLogout() {
			this.logout();
			/**
			 * 如果需要强制登录跳转回登录页面
			 */
			if (this.forcedLogin) {
				uni.reLaunch({
					url: '../login/login',
				});
			}
		},
		change(e) {
			console.log(e)
		}
	}
}
